@import url(https://fonts.googleapis.com/css2?family=Mystery+Quest&family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mystery+Quest&family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mystery+Quest&family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mystery+Quest&display=swap);
body {
  background-attachment: fixed;
  background-image: url(/Pokemon-Cards/build/static/media/pokemon_background.259fd1ec.jpg);
  background-position: center;
  background-size: cover;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  max-height: 100vh;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
    background-color: #000000;
    background-image: linear-gradient(147deg, #000000 0%, #434343 74%);
    border-radius: 20px;
    color: white;
    font-family: 'Lobster Two', cursive;
    opacity: 0.95;
    padding: 20px;
    text-transform: capitalize;
    transition: .3s
}

.card:hover {
    box-shadow: 0 0 10px #ff8c3b,
                 0 0 50px #ff8c3b,
                 0 0 10px #ff8c3b,
                 0 0 10px #ff8c3b,
                 0 0 10px #ff8c3b;
    background-color: #e84393;
    background-image: linear-gradient(315deg, #e84393 0%, #000000 74%);
    transform: scale(1.04);
}

.card-img {
    align-items: center;
    border-radius: 20px;
    display: flex;
    justify-content: space-around;
    min-height: 100px;
}

.card-img img {
    height: 100px;
    width: 100px;
}

.card-name {
    color: white;
    font-size: 20px;
    font-weight: bold;
    text-shadow: 0 0 10px #ff8c3b,
    0 0 50px #ff3bad,
    0 0 10px #ff3bad,
    0 0 10px #ff3bad,
    0 0 10px #ff3bad;

}

.card-name:hover {
    color: #ffeb3b;
    text-shadow: 0 0 10px #ff8c3b,
    0 0 40px #ff8c3b,
    0 0 80px #ff8c3b,
    0 0 160px #ff8c3b,
    0 0 240px #ff8c3b;
}

.card-types {
    align-items: center;
    color: white;
    display: flex;
    height: 30px;
    justify-content: space-around;
}

.card-info {
    padding: 10px 10px;
}

.card-data {
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.card-data .title {
    font-size: 16px;
    font-weight: bold;
}
.navbar {
    align-items: center;
    background-color: #e84393;
    background-image: linear-gradient(to bottom, #e84393 0%, #000000 90%);
    display: flex;
    height: 60px;
    justify-content:center;
    padding: 0px 20px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}

.nav-title {
    color: #ffeb3b;
    font-size: 30px;
    font-family: 'Trocchi', serif;
    text-align: center;
    text-shadow: 0 0 10px #ff8c3b,
                 0 0 40px #ff8c3b,
                 0 0 80px #ff8c3b,
                 0 0 160px #ff8c3b,
                 0 0 240px #ff8c3b;
}
.footer {
    align-items: center;
    background-color: #e84393;
    background-image: linear-gradient(to top, #e84393 0%, #000000 90%);
    bottom: 0;
    display: flex;
    height: 50px;
    justify-content:center;
    padding: 0px 20px;
    position: fixed;
    width: 100%;
}
 
.footer-content {
    align-items: center;
    display: flex;
    justify-content: center;
}

.footer-title {
    color: #ffeb3b;
    font-size: 24px;
    font-family: 'Trocchi', serif;
    text-align: center;
    text-shadow: 0 0 10px #ff8c3b,
                 0 0 40px #ff8c3b,
                 0 0 80px #ff8c3b,
                 0 0 160px #ff8c3b,
                 0 0 240px #ff8c3b;
}

.heart-icon {
    color: tomato;
    font-size: 30px;
}
.pokemon-card {
    margin-left: 24%;
    text-align: center;
    width: 50%;
}

@media only screen and (max-width: 768px) {
    .pokemon-card {
        margin-left: unset;
        width: unset;
    }
}

@media only screen and (max-height: 768px) {
    .pokemon-card {
        margin-top: 5%;
    }
}

@media only screen and (min-height: 769px) {
    .pokemon-card {
        margin-top: 10%;
    }
}
.main-container {
  height: calc(100vh - 110px);
  overflow-y: scroll;
  position: relative;
  scroll-behavior: smooth;
}

.main-container::-webkit-scrollbar-track {
  background-color: black;
  border-radius: 10px;
}

.main-container::-webkit-scrollbar {
  background-color: black;
  border-radius: 10px;
  -webkit-transition: .3s;
  transition: .3s;
  width: 12px;
}

.main-container::-webkit-scrollbar-thumb {
  background-color: #e84393;
  background-image: linear-gradient( #e84393 100%, #000000 100%);
  border-radius: 5px;
}

.main-container::-webkit-scrollbar-thumb:hover {
  background-color: #ff8c3b;
  background-image: linear-gradient( #ff8c3b 100%, #000000 100%);
}

.main-container::-webkit-scrollbar-thumb:hover {
  background-color: #ff8c3b;
}

.grid-container {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  margin: 0 auto;  
  min-height: 450px;
  width: 70%;
}

.button-input {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 25px;
  opacity: 0.5;
}

.button-input input[type='text'] {
  background-color: #000000;
  background-image: linear-gradient(147deg, #000000 0%, #434343 74%);
  border-radius: 5px;
  color: white;
  font-size: 16px;
  height: 30px;
  padding: 5px;
  transition: .3s;
  width: 300px;
}

.button-input input[type='text']:hover {
  border-color: #ff8c3b;
  box-shadow: 0 0 10px #ff8c3b,
  0 0 50px #ff8c3b,
  0 0 10px #ff8c3b,
  0 0 10px #ff8c3b,
  0 0 10px #ff8c3b;
}

:-ms-input-placeholder {
  color: white;
}

::placeholder {
  color: white;
}

.btn {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.btn-common button {
  background-color: #fc5296;
  background-image: linear-gradient(315deg, #fc5296 0%, #f67062 74%); 
  border: none;
  border-radius: 8px;
  color: white;
  font-size: 18px;
  height: 40px;
  margin: 0px 10px;
  width: 150px;
}

.btn-common button:hover {
  background-color: #f7b42c;
  background-image: linear-gradient(315deg, #f7b42c 0%, #fc575e 74%);
  border-radius: 12px;
  color: white;
  font-size: 20px;
  transform: scale(0.95);
  transition: .3s;
}

.loading-text {
  color: wheat;
  text-align: center;
}

.no-result {
  animation: blur 2s ease-out infinite;
  border-radius: 20px;
  color: #fff;
  font: 500 80px 'Mystery Quest', cursive;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  opacity: 0.9;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0px 0px 5px #fff, 0px 0px 7px #fff;
}

@keyframes blur {
  from {
    text-shadow:0px 0px 10px #fff,
      0px 10px 100px #7B96B8,
      0px 10px 100px #7B96B8,
      0px -10px 100px #7B96B8;
  }
}

.pagination-arrows {
  border-radius: 50%;
  color: white;
  font-size: 50px;
  margin: 180px 50px;
  position: fixed;
  transition: .3s;
}

.pagination-arrows:hover {
  color: #ff8c3b;
  text-shadow: 0 0 10px #ff8c3b,
               0 0 50px #ff8c3b,
               0 0 10px #ff8c3b,
               0 0 10px #ff8c3b,
               0 0 10px #ff8c3b;
  transform: scale(1.2);
}

.left-arrow {
  float: left;
  left: 0;
}

.right-arrow {
  float: right;
  right: 0;
}


.scrollToTop {
  animation: fadeIn 700ms ease-in-out 1s both;
  bottom: 0;
  position: fixed;
  right: 0;
}

.up-icon {
  bottom: 0;
  color: white;
  float: right;
  font-size: 40px;
  position: fixed;
  margin: auto 60px 80px auto;
  right: 0;
}

@keyframes behindblur {
  from {
    box-shadow:0px 0px 10px #fff,
      0px 0px 10px #fff, 
      0px 0px 25px #fff,
      0px 0px 25px #fff,
      0px 0px 25px #fff,
      0px 0px 50px #7B96B8,
      0px 0px 150px #7B96B8,
      0px 10px 100px #7B96B8,
      0px 10px 100px #7B96B8,
      0px 10px 100px #7B96B8,
      0px 10px 100px #7B96B8,
      0px -10px 100px #7B96B8,
      0px -10px 100px #7B96B8;
  }
}

.behind-up-icon {
  animation: behindblur 2s ease-out infinite;
  border-radius: 50%;
  bottom: 0;
  box-shadow: 0px 0px 5px #fff, 0px 0px 7px #fff;
  float: right;
  height: 20px;
  margin: auto 70px 100px auto;
  opacity: 0.8;
  position: fixed;
  right: 0;
  width: 20px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.scrollToTop:hover {
  transform: scale(1.01);
}

.hidden {
  display: none;
}

@media only screen and (min-width: 1920px) {
  .pagination-arrows {
    margin: 410px 50px;
  }
}

@media only screen and (max-width: 768px) {
  .pagination-arrows {
    margin: 220px 0px;
  }
}
